<template>
  <div>
    <Header></Header>
    <main>
      <div>
        <span class="user-name">hello</span>
      </div>
<!--      <aplayer autoplay :muisc="{title:'hahha',artist:'wolf',src:'https://img.caibeitv.com/%E6%99%93%E4%B8%B9%EF%BC%9A%E5%A4%AB%E5%A6%BB%E5%90%B5%E6%9E%B6%E7%9A%84%E5%A4%84%E7%90%86.mp3',pic:''}"></aplayer>-->
    </main>
    <Footer></Footer>
  </div>
</template>
<script>
import Header from '../components/header'
import Footer from '../components/footer'
  import Pagination from '../components/pagination'
  export default {
    components: {
      Header,
      Footer,
      Pagination,
    },
    data() {
      return {
        userInfo: {},
        userArticle: {},
        userFollow: {},
        userFans: {},
        tabIndex: 1,
        isRender: false, // 重新渲染分页组件（定位到第一页）
        audioUrl:''
      };
    },
    created() {
      this.getUserInfo();
      this.getUserArticleList();
    },
    filters: {
      formatImgUrl(url) {
        var newUrl = url.split(",")[0];
        return newUrl;
      }
    },
    watch: {
      $route(to, from) {
        this.getUserInfo();
        this.changeTabs(1);
      }
    },
    methods: {
      // 获取用户信息
      getAudiUrl() {
        this.audioUrl = this.$route.params.src;

      }
    }
  };
</script>
<style lang="less">

</style>



