    <Footer></Footer>
  </div>
</template>
<script>
  import Header from '../components/header'
  import Footer from '../components/footer'
  import Pagination from '../components/pagination'
  export default {
    components: {
      Header,
      Footer,
      Pagination
    },
    data() {
      return {
        userInfo: {},
        userArticle: {},
        userFollow: {},
        userFans: {},
        tabIndex: 1,
        isRender: false // 重新渲染分页组件（定位到第一页）
      };
    },
    created() {
      this.getUserInfo();
      this.getUserArticleList();
    },
    filters: {
      formatImgUrl(url) {
        var newUrl = url.split(",")[0];
        return newUrl;
      }
    },
    watch: {
      $route(to, from) {
        this.getUserInfo();
        this.changeTabs(1);
      }
    },
    methods: {
      // 获取用户信息
      getAudiUrl() {
        let userId = this.$route.params.src;

      }
    }
  };
</script>
